.Footer {
    display: inline-block;
    width: 40rem;
}

.Button {
    font: inherit;
    background-color: #ff2058;
    padding:0.5rem 2rem;
    color: white;
    border: 1px solid #ff2058;
    margin: 30px ;
    float: right;
    border-radius: 5px;
    cursor: pointer;
  }
  
  Button:hover,
  Button:active {
    box-shadow: 0 2px 4px rgb(0, 163, 250);
  
  }
  
  Button:focus {
    outline: none;
  }

  .Twitt {
      color: white;
      font-size: 17px;
      background-color: #ff2058;
      border-radius: 3px;
      padding: 10px;
      display: block;
      float: left;
      margin: 30px;
  }

  a:hover,
  a:active {
    box-shadow: 0 2px 4px rgb(0, 163, 250);
  
  }

  .Tmbl {
    color: white;
    font-size: 17px;
    background-color: #ff2058;
    border-radius: 3px;
    padding: 10px;
    display: block;
    float: left;
    margin: 30px;
    margin-left: 0;
}
  
  
  